import { Input, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {Service} from '../../modelos/service.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import { SpaceType } from 'src/app/modelos/spacetype.model';
import { HotelSpace } from 'src/app/modelos/hotelspace.model';
import { HotelSpaceType } from 'src/app/modelos/hotelspacetype.model';
import { ServiceName } from 'src/app/modelos/servicename.model';

@Component({
  selector: 'app-regdescespacio',
  templateUrl: './CapacidadHotel.component.html',
  styleUrls: ['./../publicaciones.component.css']
})

export class CapacidadHotelComponent implements OnInit 
{
  @Input()InfoService: Service;
  @Input()InfoServiceName: ServiceName;
  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;

  InfoHotelSpace: HotelSpace;
  public NombreSubTipoServicio : string;
  
  formularioCapacidadHotel: FormGroup;
  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    config: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, 
    private genStorageHotelSpace: GenericStorageService<HotelSpace[]>, 
    private genStorage : GenericStorageService<Service>) 
  { 
    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.NombreTipoServicio = "Hotel";
    this.UrlPrev = "/publicaciones/ServiciosHotel";

    this.InfoService = this.genStorage.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

    this.InfoServiceName = this.genStorage.getInformation("InfoServiceName_"+this.NombreTipoServicio);
    if(!this.InfoServiceName)
      this.InfoServiceName = new ServiceName();
    
    this.InfoHotelSpace =  new HotelSpace();
    this.InfoHotelSpace.RoomCount = 0;
    this.InfoHotelSpace.PeopleCount = 0;
    this.InfoHotelSpace.HotelName = this.InfoServiceName.Name;
    
    var auxHotelSpace : HotelSpace[] = this.genStorageHotelSpace.getInformation("InfoHotelSpace");
    this.InfoHotelSpace = (!auxHotelSpace || auxHotelSpace == null || auxHotelSpace.length <= 0) ? new HotelSpace() : auxHotelSpace[0];
    this.cacheManager.getData<SpaceType>("SpaceType", (array : SpaceType[]) => 
    { 
      var indexPos = array.findIndex(x=>x.IdSpaceType == this.InfoHotelSpace.IdSpaceType);
      if(indexPos != 1)
      {
          this.NombreSubTipoServicio = array[indexPos].Name.toLowerCase();
      }
    });
  }

  ngOnInit() 
  {
    this.formularioCapacidadHotel = this.fb.group({
      RoomCount: ['', Validators.compose([Validators.required, Validators.min(1)])],
      PeopleCount: ['', Validators.compose([Validators.required,  Validators.min(1)])]
      ,HotelName:  ['', null]
    });
  }

  get f()
  {
    let respuesta = this.formularioCapacidadHotel;
    return respuesta.controls;
  }

  public GuardarInfo() {
  	if (this.formularioCapacidadHotel.invalid){
      return;
    } 
    
    var auxHotelSpace : HotelSpace[] = [];
    auxHotelSpace.push(this.InfoHotelSpace);

    this.genStorageHotelSpace.setInformation("InfoHotelSpace",auxHotelSpace);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public Continuar()
  {
    this.GuardarInfo();

    this.UrlNext = "/publicaciones/DetalleHabitacion"  + '/' + this.NombreTipoServicio + "/" + this.NombreSubTipoServicio + "/" + this.InfoHotelSpace.RoomCount + "/0/1";
    this.router.navigate([this.UrlNext]);
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }
}
