import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';

import {Service} from '../../modelos/service.model';
import {ServiceTransport} from '../../modelos/servicetransport.model';
import {TransportType} from '../../modelos/transporttype.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { ListaSeleccionComponent } from 'src/app/utilidades/ListaSeleccion/ListaSeleccion.component';

@Component({
  selector: 'app-reginfobasica',
  templateUrl: './TransporteCercano.component.html',
  styleUrls: ['./TransporteCercano.component.scss','./../publicaciones.component.css']
})

export class TransporteCercanoComponent implements OnInit 
{
  InfoService: Service;
  
  Configuration: any;
  InfoTransportService: ServiceTransport[] = [];
  @ViewChild(ListaSeleccionComponent) compListaTipoTransporte: ListaSeleccionComponent<ServiceTransport,TransportType>;

  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  
  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(private rutaActiva: ActivatedRoute, private router:Router,
    config: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, private storage: StorageServiceService, 
    private genStorageServ : GenericStorageService<Service>,
    private genStorageTrans : GenericStorageService<ServiceTransport[]>) 
  { 
    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.NombreTipoServicio = this.rutaActiva.snapshot.params.tipoServicio;
    this.UrlPrev = "/publicaciones/InformacionUbicacion" + '/' + this.NombreTipoServicio;
    this.UrlNext = "/publicaciones/HorariosAtencion" + '/' + this.NombreTipoServicio;

    if(this.NombreTipoServicio == "Hotel")
    {
      this.UrlNext = "/publicaciones/AtraccionesCercanas" + '/' + this.NombreTipoServicio;
    }

    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

      this.Configuration = 
      {
          NameFieldId: "IdTransportType",
          StorageKey: "InfoServiceTransport_" + this.NombreTipoServicio,
          MasterEntityName : "TransportType",
          AdditionalFieldName : "Description",
          AdditionalFieldType : "text",
          AdditionalFieldLabel: "Indique una breve descripcin"
      };
      this.compListaTipoTransporte = new ListaSeleccionComponent<ServiceTransport,TransportType>(cacheManager, genStorageTrans);
  }

  ngOnInit() {    
  }

  public GuardarInfo() 
  {    
    this.GetStorageEntity();
    this.genStorageTrans.setInformation("InfoServiceTransport_"+this.NombreTipoServicio,this.InfoTransportService);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public Continuar()
  {
    if(this.compListaTipoTransporte.ValidarDescripcionesDiligenciadas())
    {
      this.GuardarInfo();
      this.router.navigate([this.UrlNext]);
    }
    else
    {
      this.mensaje = [{ message : "Especifique primero una descripcin para cada transporte seleccionado." , id : 1, type : "warning" }];
    }
  }

  public GetStorageEntity()
  {
      var listaEntidad : ServiceTransport[] = [];
      this.compListaTipoTransporte.InfoStorageEntity.forEach(itemComp => 
      {
          var itemEntidad : ServiceTransport = new ServiceTransport();
          itemEntidad.IdTransportType = itemComp.IdTransportType;
          itemEntidad.Description = itemComp.Description;
          
          listaEntidad.push(itemEntidad);
      });

      this.InfoTransportService = listaEntidad;
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }
}
