import { Input, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {Service} from '../../modelos/service.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import { SpaceType } from 'src/app/modelos/spacetype.model';
import { AccommodationSpace } from 'src/app/modelos/accommodationspace.model';
import { AccommodationSpaceType } from 'src/app/modelos/accommodationspacetype.model';

@Component({
  selector: 'app-regdescespacio',
  templateUrl: './CapacidadAlojamiento.component.html',
  styleUrls: ['./../publicaciones.component.css']
})

export class CapacidadAlojamientoComponent implements OnInit 
{
  @Input()InfoService: Service;
  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;

  InfoAccommodationSpace: AccommodationSpace;
  public NombreSubTipoServicio : string;
  
  formularioCapacidadAlojamiento: FormGroup;
  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    config: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, 
    private genStorageAccommodationSpace: GenericStorageService<AccommodationSpace[]>, 
    private genStorage : GenericStorageService<Service>) 
  { 
    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.NombreTipoServicio = "Alojamiento";
    this.UrlPrev = "/publicaciones/ServiciosAlojamiento";

    this.InfoService = this.genStorage.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();
    
    this.InfoAccommodationSpace =  new AccommodationSpace();
    this.InfoAccommodationSpace.RoomCount = "0";
    this.InfoAccommodationSpace.PeopleCount = "0";

    var auxAccommodationSpace : AccommodationSpace[] = this.genStorageAccommodationSpace.getInformation("InfoAccommodationSpace");
    this.InfoAccommodationSpace = (!auxAccommodationSpace || auxAccommodationSpace == null || auxAccommodationSpace.length <= 0) ? this.InfoAccommodationSpace : auxAccommodationSpace[0];
    this.cacheManager.getData<AccommodationSpaceType>("AccommodationSpaceType", (array : AccommodationSpaceType[]) => 
    { 
      var indexPos = array.findIndex(x=>x.IdAccommodationSpaceType == this.InfoAccommodationSpace.IdAccommodationSpaceType);
      if(indexPos != 1)
      {
          this.NombreSubTipoServicio = array[indexPos].Name.toLowerCase();
      }
    });
  }

  ngOnInit() 
  {
    this.formularioCapacidadAlojamiento = this.fb.group({
      RoomCount: ['', Validators.compose([Validators.required, Validators.min(1)])],
      PeopleCount: ['', Validators.compose([Validators.required,  Validators.min(1)])]
    });
  }

  get f()
  {
    let respuesta = this.formularioCapacidadAlojamiento;
    return respuesta.controls;
  }

  public GuardarInfo() {
  	if (this.formularioCapacidadAlojamiento.invalid){
      return;
    } 
    
    var auxAccommodationSpace : AccommodationSpace[] = [];
    auxAccommodationSpace.push(this.InfoAccommodationSpace);

    this.genStorageAccommodationSpace.setInformation("InfoAccommodationSpace",auxAccommodationSpace);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public Continuar()
  {
    this.GuardarInfo();

    this.UrlNext = "/publicaciones/DetalleHabitacion"  + '/' + this.NombreTipoServicio + "/" + this.NombreSubTipoServicio + "/" + this.InfoAccommodationSpace.RoomCount + "/0/1";
    this.router.navigate([this.UrlNext]);
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }
}
