import { Input, Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { Router } from '@angular/router';

import {Service} from '../../modelos/service.model';
import {HotelSpace} from '../../modelos/hotelspace.model';
import {SpaceType} from '../../modelos/spacetype.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { ListaSeleccionComponent } from 'src/app/utilidades/ListaSeleccion/ListaSeleccion.component';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import { HotelSpaceType } from 'src/app/modelos/hotelspacetype.model';
import { ExclusivenessType } from 'src/app/modelos/exclusivenesstype.model';

@Component({
  selector: 'app-regservhotel',
  templateUrl: './ServiciosHotel.component.html',
  styleUrls: ['./ServiciosHotel.component.css','./../publicaciones.component.css']
})

export class ServiciosHotelComponent implements OnInit 
{
  InfoService: Service;
  InfoSpaceType: SpaceType;

  Configuration: any;
  InfoHotelSpace: HotelSpace[] = [];
  @ViewChild(ListaSeleccionComponent) compListaSpaceType: ListaSeleccionComponent<HotelSpace,SpaceType>;

  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  public NombreTipoHotel : string;

  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private router:Router,
    config: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, 
    private storage: StorageServiceService, 
    private genStorageServ : GenericStorageService<Service>,
    private genStorageSpaceType : GenericStorageService<SpaceType[]>,
    private genStorageClasificacion : GenericStorageService<number>,
    private genStorageHotelSpace : GenericStorageService<HotelSpace[]>) 
  { 
    this.NombreTipoServicio = "Hotel";
    //this.UrlPrev = "/publicaciones/ClasificacionHotel";
    this.UrlPrev = "/publicaciones/ImagenPrincipal/" + this.NombreTipoServicio;
    this.UrlNext = "/publicaciones/CapacidadHotel";

    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

    this.Configuration = 
    {
        NameFieldId: "IdSpaceType",
        StorageKey: "InfoHotelSpace",
        MasterEntityName : "SpaceType",
        AdditionalFieldName : "",
        AdditionalFieldType : "",
        AdditionalFieldLabel: "",
        Filters: [{field: "IdServiceType", value: this.InfoService.IdServiceType}],
        EsSeleccionUnica: true
    };
    this.compListaSpaceType = new ListaSeleccionComponent<HotelSpace,SpaceType>(cacheManager, genStorageHotelSpace);
  }

  ngOnInit() 
  {
  }

  public GuardarInfo() 
  {    
    this.GetStorageEntity();
    this.genStorageHotelSpace.setInformation("InfoHotelSpace",this.InfoHotelSpace);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public Continuar()
  {
    if (this.compListaSpaceType.InfoStorageEntity == null || this.compListaSpaceType.InfoStorageEntity.length <= 0)
    {
        this.mensaje = [{ id:1, type:'warning', message: "Seleccione por lo menos un servicio para su Hotel!" }];  
        return;
    }
    else if(this.compListaSpaceType.ValidarDescripcionesDiligenciadas())
    {
        this.GuardarInfo();
        this.router.navigate([this.UrlNext]);
    }
    else
    {
        this.mensaje = [{ message : "Especifique la capacidad de personas permitidas para cada servicio seleccionado." , id : 1, type : "warning" }];
    }
  }

  public GetStorageEntity()
  {
      var InfoClasificacionHotel : number = this.genStorageClasificacion.getInformation("ClasificacionHotel");

      var listaEntidad : HotelSpace[] = [];
      this.compListaSpaceType.InfoStorageEntity.forEach(itemComp => 
      {
          var itemEntidad : HotelSpace = new HotelSpace();
          itemEntidad.IdSpaceType = itemComp.IdSpaceType;
          itemEntidad.StarCount = InfoClasificacionHotel;
          itemEntidad.HotelName = itemComp.HotelName;
          itemEntidad.PeopleCount = itemComp.PeopleCount;
	        itemEntidad.RoomCount = itemComp.RoomCount;
          
          listaEntidad.push(itemEntidad);
      });

      this.InfoHotelSpace = listaEntidad;
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }

}
