import { Input, Component, OnInit, ViewChild  } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {SpaceType} from '../../modelos/spacetype.model';
import {Service} from '../../modelos/service.model';
import {CoWorkingSpace} from '../../modelos/coworkingspace.model';
import {ServiceSpaceItems} from '../../modelos/ServiceSpaceItems.model';
import {SpaceItemType} from '../../modelos/spaceitemtype.model';
import {ServiceSpaceContract} from '../../modelos/ServiceSpaceContract.model';
import {ContractType} from '../../modelos/contracttype.model';
import { ServiceSpaceBenefit } from '../../modelos/servicespacebenefit.model';
import { BenefitType } from '../../modelos/benefittype.model';
import { File } from '../../modelos/file.model';
import { ServiceSpaceMultimedia } from '../../modelos/servicespacemultimedia.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { UploadComponent } from '../../utilidades/file-upload/upload.component';
import { ListaSeleccionComponent } from '../../utilidades/ListaSeleccion/ListaSeleccion.component';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import {UrlBackendServices} from './../../services/AppServiceGlobal';
import { PublicacionesServiceService } from '../../services/Publicaciones-service.service';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import {ImagePicker, ImagePickerOptions} from '@ionic-native/image-picker/ngx';

@Component({
  selector: 'app-regservicios',
  templateUrl: './ImagenPrincipal.component.html',
  styleUrls: ['./ImagenPrincipal.component.css','./../publicaciones.component.css']
})

export class ImagenPrincipalComponent implements OnInit 
{
  InfoService: Service;
  mensaje: any = [];
  
  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  public NombreServicio : string;  
  
  InfoServiceSpaceMultimedia : ServiceSpaceMultimedia[] = [];
  @ViewChild(UploadComponent) compCargarImagenes: UploadComponent;
  public ImageServiceURL : string = UrlBackendServices + "/api/Image";
  public IndiceServicio : number = 99999;
  public FileOptions : File;

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    config1: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, 
    private storage: StorageServiceService, 
    private genStorageServ : GenericStorageService<Service>,
    private genStorageServiceSpaceMultimedia : GenericStorageService<ServiceSpaceMultimedia[]>,

    private serviceImages: PublicacionesServiceService,
    private imagePicker: ImagePicker,
    private camera: Camera) 
  { 
    config1 = new NgbProgressbarConfig();
    config1.max = 80;
    config1.striped = true;
    config1.animated = true;
    config1.type = 'success';

    this.NombreTipoServicio = this.rutaActiva.snapshot.params.tipoServicio;
    this.FileOptions = new File();

    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

    if(this.NombreTipoServicio == "CoWorking")
    {
        this.UrlPrev = "/publicaciones/Beneficios/CoWorking";
        this.UrlNext = "/publicaciones/ServiciosCoWorking";
    }
    else if(this.NombreTipoServicio == "Hotel")
    {
        this.UrlPrev = "/publicaciones/ClasificacionHotel";
        this.UrlNext = "/publicaciones/ServiciosHotel";
    }

    this.compCargarImagenes = new UploadComponent(this.storage,this.serviceImages,this.genStorageServiceSpaceMultimedia, this.imagePicker, this.camera);
  }

  ngOnInit() 
  {
    this.ActualizarInformacionEncabezados();
  }

  public GuardarInfo() 
  {    
    this.GetStorageEntity_ServiceSpaceMultimedia();
    this.genStorageServiceSpaceMultimedia.setInformation("InfoServiceSpaceMultimedia_" + this.NombreTipoServicio + "_" + this.IndiceServicio,this.InfoServiceSpaceMultimedia);

    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public GetStorageEntity_ServiceSpaceMultimedia()
  {
      this.InfoServiceSpaceMultimedia = this.compCargarImagenes.InfoServiceSpaceMultimedia;
  }

  public Continuar()
  {
      this.GuardarInfo();
      this.router.navigate([this.UrlNext]);
      this.ActualizarInformacionEncabezados();
  }

  public Regresar()
  {
      this.GuardarInfo();
      this.router.navigate([this.UrlPrev]);
      this.ActualizarInformacionEncabezados();
  }

  public ActualizarInformacionEncabezados()
  {
    this.compCargarImagenes.NombreTipoServicio = this.NombreTipoServicio;
    this.compCargarImagenes.ServiceUrl = this.ImageServiceURL;
    this.compCargarImagenes.ServiceIndex = this.IndiceServicio;

    this.mensaje = [];
  }
}
