import { Input, Component, ViewChild, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {Service} from '../../modelos/service.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { ListaSeleccionComponent } from 'src/app/utilidades/ListaSeleccion/ListaSeleccion.component';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import { ExclusivenessType } from 'src/app/modelos/exclusivenesstype.model';

@Component({
  selector: 'app-regservcowork',
  templateUrl: './TipoExclusividad.component.html',
  styleUrls: ['./TipoExclusividad.component.css','./../publicaciones.component.css']
})

export class TipoExclusividadComponent implements OnInit 
{
  InfoService: Service;

  Configuration: any;
  InfoExclusivenessType: ExclusivenessType[] = [];
  @ViewChild(ListaSeleccionComponent) compListaTipoExclusividad: ListaSeleccionComponent<ExclusivenessType,ExclusivenessType>;

  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  
  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    config: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, private storage: StorageServiceService, 
    private genStorageServ : GenericStorageService<Service>,
    private genStorageExclusivenessType : GenericStorageService<ExclusivenessType[]>) 
  { 
    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.NombreTipoServicio = "Alojamiento";
    this.UrlPrev = "/publicaciones/HorariosAtencion" + '/' + this.NombreTipoServicio;
    this.UrlNext = "/publicaciones/TipoAlojamiento";

    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

    this.Configuration = 
    {
        NameFieldId: "IdExclusivenessType",
        StorageKey: "InfoExclusivenessType_" + this.NombreTipoServicio,
        MasterEntityName : "ExclusivenessType",
        AdditionalFieldName : "",
        AdditionalFieldType : "",
        AdditionalFieldLabel: "",
        EsSeleccionUnica: true
    };
    this.compListaTipoExclusividad = new ListaSeleccionComponent<ExclusivenessType,ExclusivenessType>(cacheManager, genStorageExclusivenessType);
  }

  ngOnInit() 
  {
  }

  public GuardarInfo() 
  {    
    this.GetStorageEntity();
    this.genStorageExclusivenessType.setInformation("InfoExclusivenessType_"+this.NombreTipoServicio,this.InfoExclusivenessType);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public GetStorageEntity()
  {
      var listaEntidad : ExclusivenessType[] = [];
      this.compListaTipoExclusividad.InfoStorageEntity.forEach(itemComp => 
      {
          var itemEntidad : ExclusivenessType = new ExclusivenessType();
          itemEntidad.IdExclusivenessType = itemComp.IdExclusivenessType;
          
          listaEntidad.push(itemEntidad);
      });

      this.InfoExclusivenessType = listaEntidad;
  }


  public Continuar()
  {
    if (this.compListaTipoExclusividad.InfoStorageEntity == null || this.compListaTipoExclusividad.InfoStorageEntity.length <= 0)
    {
        this.mensaje = [{ id:1, type:'warning', message: "Seleccione una respuesta a la pregunta!" }];  
        return;
    }
    
    this.GuardarInfo();
    this.router.navigate([this.UrlNext]);
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }
}
