import { Input, Component, OnInit, ViewChild  } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {Service} from '../../modelos/service.model';
import {AccommodationSpace} from '../../modelos/accommodationspace.model';
import {ServiceSpaceItems} from '../../modelos/ServiceSpaceItems.model';
import {SpaceItemType} from '../../modelos/spaceitemtype.model';
import {ServiceSpaceContract} from '../../modelos/ServiceSpaceContract.model';
import {ContractType} from '../../modelos/contracttype.model';
import { ServiceSpaceBenefit } from '../../modelos/servicespacebenefit.model';
import { BenefitType } from '../../modelos/benefittype.model';
import { File } from '../../modelos/file.model';
import { ServiceSpaceMultimedia } from '../../modelos/servicespacemultimedia.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { UploadComponent } from '../../utilidades/file-upload/upload.component';
import { ListaSeleccionComponent } from '../../utilidades/ListaSeleccion/ListaSeleccion.component';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import {UrlBackendServices} from './../../services/AppServiceGlobal';
import { PublicacionesServiceService } from '../../services/Publicaciones-service.service';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import {ImagePicker, ImagePickerOptions} from '@ionic-native/image-picker/ngx';
import { AccommodationSpaceType } from 'src/app/modelos/accommodationspacetype.model';

@Component({
  selector: 'app-regservicios',
  templateUrl: './DetalleHotel.component.html',
  styleUrls: ['./DetalleHotel.component.css','./../publicaciones.component.css']
})

export class DetalleHotelComponent implements OnInit 
{
  InfoService: Service;
  static IsEquasError:boolean = false;
  mensaje: any = [];

  Configuration1: any;
  InfoServiceSpaceItems	: ServiceSpaceItems[] = [];
  @ViewChild(ListaSeleccionComponent) compListaSpaceItems: ListaSeleccionComponent<ServiceSpaceItems,SpaceItemType>;
  
  Configuration2: any;
  InfoServiceSpaceContract : ServiceSpaceContract[] = [];
  @ViewChild(ListaSeleccionComponent) compListaSpaceContract: ListaSeleccionComponent<ServiceSpaceContract,ContractType>;

  Configuration3: any;
  InfoServiceSpaceBenefit : ServiceSpaceBenefit[] = [];
  @ViewChild(ListaSeleccionComponent) compListaSpaceBenefit	: ListaSeleccionComponent<ServiceSpaceBenefit,BenefitType>;
  
  InfoServiceSpaceMultimedia : ServiceSpaceMultimedia[] = [];
  InfoAccommodationSpace: AccommodationSpace[] = [];
  accommodationSpaceType: AccommodationSpaceType[] = [];

  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  public NombreServicio : string
  public IndiceServicio : number;
  public IndiceProceso : number;
  public CantidadServiciosAgregados : number = 10;
  
  @ViewChild(UploadComponent) compCargarImagenes: UploadComponent;
  public ImageServiceURL : string = UrlBackendServices + "/api/Image";
  public FileOptions : File;

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    config1: NgbProgressbarConfig,
    config2: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, 
    private storage: StorageServiceService, 
    private genStorageServ : GenericStorageService<Service>,
    private genStorageAccommodationSpace : GenericStorageService<AccommodationSpace[]>,
    private genStorageServiceSpaceItems : GenericStorageService<ServiceSpaceItems[]>,
    private genStorageServiceSpaceBenefit : GenericStorageService<ServiceSpaceBenefit[]>,
    private genStorageServiceSpaceContract : GenericStorageService<ServiceSpaceContract[]>,
    private genStorageServiceSpaceMultimedia : GenericStorageService<ServiceSpaceMultimedia[]>,

    private serviceImages: PublicacionesServiceService,
    private imagePicker: ImagePicker,
    private camera: Camera) 
  { 
    config1 = new NgbProgressbarConfig();
    config1.max = 80;
    config1.striped = true;
    config1.animated = true;
    config1.type = 'success';

    config2 = new NgbProgressbarConfig();
    config2.max = 80;
    config2.striped = true;
    config2.animated = true;
    config2.type = 'danger';

    this.NombreTipoServicio = "Alojamiento";
    this.IndiceServicio = this.rutaActiva.snapshot.params.idServicio;
    this.IndiceProceso = this.rutaActiva.snapshot.params.idProceso;

    this.FileOptions = new File();
    
    this.Configuration1 = 
    {
        NameFieldId: "IdSpaceItemType",
        StorageKey: "InfoServiceSpaceItems_" + this.NombreTipoServicio + "_" + this.IndiceServicio,
        MasterEntityName : "SpaceItemType",
        AdditionalFieldName : "ItemCount",
        AdditionalFieldType : "number",
        AdditionalFieldLabel: "Qu cantidad de este elemento dispone?",
        FiltersOperator: "OR",
        Filters: [{field: "Category", value: this.NombreTipoServicio}, {field: "Category", value: ""} ]
    };
    this.compListaSpaceItems = new ListaSeleccionComponent<ServiceSpaceItems,SpaceItemType>(cacheManager, genStorageServiceSpaceItems);

    this.Configuration2 = 
    {
        NameFieldId: "IdContractType",
        StorageKey: "InfoServiceSpaceContract_" + this.NombreTipoServicio + "_" + this.IndiceServicio,
        MasterEntityName : "ContractType",
        AdditionalFieldName : "ContractValue",
        AdditionalFieldType : "number",
        AdditionalFieldLabel: "Valor para este tipo de contrato",
        FiltersOperator: "OR",
        Filters: [{field: "Categoria", value: this.NombreTipoServicio} ]
    };
    this.compListaSpaceContract = new ListaSeleccionComponent<ServiceSpaceContract,ContractType>(cacheManager, genStorageServiceSpaceContract);

    this.Configuration3 = 
    {
        NameFieldId: "IdBenefitType",
        StorageKey: "InfoServiceSpaceBenefit_" + this.NombreTipoServicio + "_" + this.IndiceServicio,
        MasterEntityName : "BenefitType",
        AdditionalFieldName : "",
        AdditionalFieldType : "",
        AdditionalFieldLabel: "",
        FiltersOperator: "OR",
        Filters: [{field: "Category", value: this.NombreTipoServicio}, {field: "Category", value: ""} ]
    };
    this.compListaSpaceBenefit = new ListaSeleccionComponent<ServiceSpaceBenefit,BenefitType>(cacheManager, genStorageServiceSpaceBenefit);

    this.compCargarImagenes = new UploadComponent(this.storage,this.serviceImages,this.genStorageServiceSpaceMultimedia, this.imagePicker, this.camera);

    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

    this.InfoAccommodationSpace = this.genStorageAccommodationSpace.getInformation("InfoAccommodationSpace");
    if(!this.InfoAccommodationSpace || this.InfoAccommodationSpace == null)
        this.InfoAccommodationSpace = [];

    this.cacheManager.getData<AccommodationSpaceType>("AccommodationSpaceType", (array : AccommodationSpaceType[]) => { this.accommodationSpaceType = array; });    
    this.CantidadServiciosAgregados = this.InfoAccommodationSpace.length;
  }

  ngOnInit() 
  {
    this.ActualizarInformacionEncabezados();
  }

  public GuardarInfo() 
  {    
    if(this.IndiceProceso == 1)
    {
        this.GetStorageEntity_ServiceSpaceItems();
        this.genStorageServiceSpaceItems.setInformation("InfoServiceSpaceItems_" + this.NombreTipoServicio + "_" + this.IndiceServicio,this.InfoServiceSpaceItems);
    }
    else if(this.IndiceProceso == 2)
    {
        this.GetStorageEntity_ServiceSpaceBenefit();
        this.genStorageServiceSpaceBenefit.setInformation("InfoServiceSpaceBenefit_" + this.NombreTipoServicio + "_" + this.IndiceServicio,this.InfoServiceSpaceBenefit);
    }
    else if(this.IndiceProceso == 3)
    {
        this.GetStorageEntity_ServiceSpaceMultimedia();
        this.genStorageServiceSpaceMultimedia.setInformation("InfoServiceSpaceMultimedia_" + this.NombreTipoServicio + "_" + this.IndiceServicio,this.InfoServiceSpaceMultimedia);
    }
    else if(this.IndiceProceso == 4)
    {
        this.GetStorageEntity_ServiceSpaceContract();
        this.genStorageServiceSpaceContract.setInformation("InfoServiceSpaceContract_" + this.NombreTipoServicio + "_" + this.IndiceServicio,this.InfoServiceSpaceContract);
    }

    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public GetStorageEntity_ServiceSpaceItems()
  {
      var listaEntidad : ServiceSpaceItems[] = [];
      this.compListaSpaceItems.InfoStorageEntity.forEach(itemComp => 
      {
          var itemEntidad : ServiceSpaceItems = new ServiceSpaceItems();
          itemEntidad.IdSpaceItemType = itemComp.IdSpaceItemType;
          itemEntidad.ItemCount = itemComp.ItemCount;
          
          listaEntidad.push(itemEntidad);
      });

      this.InfoServiceSpaceItems = listaEntidad;
  }

  public GetStorageEntity_ServiceSpaceBenefit()
  {
      var listaEntidad : ServiceSpaceBenefit[] = [];
      this.compListaSpaceBenefit.InfoStorageEntity.forEach(itemComp => 
      {
          var itemEntidad : ServiceSpaceBenefit = new ServiceSpaceBenefit();
          itemEntidad.IdBenefitType = itemComp.IdBenefitType;
          
          listaEntidad.push(itemEntidad);
      });

      this.InfoServiceSpaceBenefit = listaEntidad;
  }

  public GetStorageEntity_ServiceSpaceMultimedia()
  {
      var listaEntidad : ServiceSpaceMultimedia[] = [];
      this.InfoServiceSpaceMultimedia = this.compCargarImagenes.InfoServiceSpaceMultimedia;
  }

  public GetStorageEntity_ServiceSpaceContract()
  {
      var listaEntidad : ServiceSpaceContract[] = [];
      this.compListaSpaceContract.InfoStorageEntity.forEach(itemComp => 
      {
          var itemEntidad : ServiceSpaceContract = new ServiceSpaceContract();
          itemEntidad.IdContractType = itemComp.IdContractType;
          itemEntidad.ContractValue = itemComp.ContractValue;
          
          listaEntidad.push(itemEntidad);
      });

      this.InfoServiceSpaceContract = listaEntidad;
  }

  public Continuar()
  {
      if(this.IndiceProceso == 1 && !this.compListaSpaceItems.ValidarDescripcionesDiligenciadas())
      {
          this.mensaje = [{ message : "Especifique primero la cantidad de cada elemento seleccionado." , id : 1, type : "warning" }];
          return;
      }
      else if(this.IndiceProceso == 4)
      {
          if(this.compListaSpaceContract.InfoStorageEntity == null || this.compListaSpaceContract.InfoStorageEntity.length <= 0)
          {
            this.mensaje = [{ message : "Agregue primero un modo de contratacin y especifque su valor.." , id : 1, type : "warning" }];
              return;
          }
          else if(!this.compListaSpaceContract.ValidarDescripcionesDiligenciadas())
          {
              this.mensaje = [{ message : "Especifique primero un valor de cada tipo de contrato seleccionado." , id : 1, type : "warning" }];
              return;
          }
      }

      this.GuardarInfo();

      if(this.IndiceProceso < 4)
      {
        this.IndiceProceso++;
        this.UrlNext = "/publicaciones/DetalleHotel/" + this.IndiceServicio + "/" + this.IndiceProceso;      
      }
      else
      {
        if((this.IndiceServicio) >= (this.CantidadServiciosAgregados-1) )
        {
          if(this.InfoServiceSpaceContract.length <= 0)
          {
              this.mensaje = [{ message : "Agregue primero un modo de contratacin y especifque su valor.." , id : 1, type : "warning" }];
              return;
          }

          this.UrlNext = "/publicaciones/ResumenRegistro/" + this.NombreTipoServicio;
        }
        else
        {
          this.IndiceServicio++;
          this.IndiceProceso = 1;
          this.UrlNext = "/publicaciones/DetalleHotel/" + this.IndiceServicio + "/" + this.IndiceProceso;      
        }
      }

      this.router.navigate([this.UrlNext]);
      this.ActualizarInformacionEncabezados();
  }

  public Regresar()
  {
      this.GuardarInfo();

      if(this.IndiceProceso < 4)
      {
        if(this.IndiceServicio == 0 && this.IndiceProceso == 1 )
        {
          this.UrlPrev = "/publicaciones/ServiciosAlojamiento";
        }
        else if(this.IndiceProceso == 1 )
        {
          this.IndiceServicio--;
          this.IndiceProceso=4;
          this.UrlPrev = "/publicaciones/DetalleHotel/" + this.IndiceServicio + "/" + this.IndiceProceso;
        }
        else
        {
          this.IndiceProceso--;
          this.UrlPrev = "/publicaciones/DetalleHotel/" + this.IndiceServicio + "/" + this.IndiceProceso;
        }
      }
      else
      {
        this.IndiceProceso--;
        this.UrlPrev = "/publicaciones/DetalleHotel/" + this.IndiceServicio + "/" + this.IndiceProceso;
      }

      this.router.navigate([this.UrlPrev]);
      this.ActualizarInformacionEncabezados();
  }

  public ActualizarInformacionEncabezados()
  {
    var idServicio = this.InfoAccommodationSpace[this.IndiceServicio].IdAccommodationSpaceType;
    this.NombreServicio = this.accommodationSpaceType.find(x=>x.IdAccommodationSpaceType == idServicio).Name;

    if (this.IndiceProceso == 3)
    {
        this.compCargarImagenes.NombreTipoServicio = this.NombreTipoServicio;
        this.compCargarImagenes.ServiceUrl = this.ImageServiceURL;
        this.compCargarImagenes.ServiceIndex = this.IndiceServicio;
    }

    this.mensaje = [];
  }
}
