import { Input, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbTimeStruct, NgbTimeAdapter, NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';

import {Service} from '../../modelos/service.model';
import {ServiceSchedule} from '../../modelos/serviceschedule.model';
import * as tableData from './HorariosAtencion-data-table';
import * as tableData_Hoteles from './HorariosAtencion-data-table2';
import { LocalDataSource } from 'ng2-smart-table';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';

@Component({
  selector: 'app-reghorarioatencion',
  templateUrl: './HorariosAtencion.component.html',
  styleUrls: ['./HorariosAtencion.component.css']
})

export class HorariosAtencionComponent implements OnInit {
  @Input()serviceSchedule	: ServiceSchedule;
  @Input()timeStart	: any;
  @Input()timeEnd	: any;
  contentTable: any = [];
  InfoServiceSchedule: ServiceSchedule[] = [];
  InfoService: Service;

  settings: any = tableData.settings;
  source: LocalDataSource;

  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  
  formularioHorariosAtencion: FormGroup;
  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    config: NgbProgressbarConfig, 
    private cacheManager: CacheStorageService, private storage: StorageServiceService, 
    private genStorageServ : GenericStorageService<Service>,
    private genStorageSchedule : GenericStorageService<ServiceSchedule[]>) 
  { 
    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.NombreTipoServicio = this.rutaActiva.snapshot.params.tipoServicio;
    this.UrlPrev = "/publicaciones/TransporteCercano" + '/' + this.NombreTipoServicio;

    if(this.NombreTipoServicio == "Hotel")
        this.settings = tableData_Hoteles.settings;
    
    if(this.NombreTipoServicio == "CoWorking")
        this.UrlNext = "/publicaciones/Beneficios" + '/' + this.NombreTipoServicio;    
    else if(this.NombreTipoServicio == "Alojamiento")
        this.UrlNext = "/publicaciones/TipoExclusividad";
    else if(this.NombreTipoServicio == "Hotel")
    {
        this.UrlPrev = "/publicaciones/AtraccionesCercanas" + '/' + this.NombreTipoServicio;
        this.UrlNext = "/publicaciones/ClasificacionHotel";
    }
  }

  ngOnInit() {
    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

    this.ReiniciarModeloFormulario();

    this.InfoServiceSchedule = this.genStorageSchedule.getInformation("InfoServiceSchedule_"+this.NombreTipoServicio);
    if(!this.InfoServiceSchedule || this.InfoServiceSchedule == null)
        this.InfoServiceSchedule = [];
    
    this.ActualizarTabla();

    this.formularioHorariosAtencion = this.fb.group({
      SelectDiaSemana: ['', [Validators.required]],
      StartTime: ['', [Validators.required]],
      EndTime: ['', [Validators.required]],
    });
  }

  get f()
  {
    let respuesta = this.formularioHorariosAtencion;
    return respuesta.controls;
  }

  public GuardarInfo() {    
    this.genStorageSchedule.setInformation("InfoServiceSchedule_"+this.NombreTipoServicio,this.InfoServiceSchedule);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public Continuar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlNext]);
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }

  public AgregarHorario()
  {
    if (this.formularioHorariosAtencion.invalid){
      return;
    }

    this.serviceSchedule.StartTime = this.timerToModel(this.timeStart);
    this.serviceSchedule.EndTime = this.timerToModel(this.timeEnd);

    var indexSchedule = this.InfoServiceSchedule.findIndex( x =>
      x.DayName == this.serviceSchedule.DayName && 
      (
          this.timerFromModel(x.StartTime) >= this.timerFromModel(this.serviceSchedule.StartTime) && 
          this.timerFromModel(x.EndTime) <= this.timerFromModel(this.serviceSchedule.EndTime)
      )
    );
    if(indexSchedule != -1)
    {
      this.mensaje = [{ id:2, type:'warning', message: "El horario de atencin seleccionado ya ha sido agregado!" }];
      return;
    }

    this.InfoServiceSchedule.push(this.serviceSchedule);
    this.ReiniciarModeloFormulario();
    
    this.ActualizarTabla();
  }

  ReiniciarModeloFormulario()
  {
    this.serviceSchedule = new ServiceSchedule(); 
    this.serviceSchedule.DayName = "Lun-Dom";
    this.timeStart = this.timerFromModel("00:00:00");
    this.timeEnd = this.timerFromModel("23:59:59");
  }

  timerToModel(time1: NgbTimeStruct): string {
    if (!time1) {
      return null;
    }
    return `${this.pad(time1.hour)}:${this.pad(time1.minute)}`;
  }

  timerFromModel(value: string): NgbTimeStruct {
    if (!value) {
      return null;
    }
    const split = value.split(':');
    return {
      hour: parseInt(split[0], 10),
      minute: parseInt(split[1], 10),
      second: 0
    };
  }

  private pad(i: number): string {
    return i < 10 ? `0${i}` : `${i}`;
  }

  public ActualizarTabla()
  {
    this.contentTable = [];
    this.InfoServiceSchedule.forEach(element => {
      var auxItemTable : any = 
      {
        DayName : element.DayName,
        StartTime : element.StartTime,
        EndTime : element.EndTime
      };
      
      this.contentTable.push(auxItemTable);
    });

    this.source = new LocalDataSource(this.contentTable);
  }

  onDeleteConfirm(event) 
  {
    var serviceScheduleAux : any = event.data;
    var posArray = this.InfoServiceSchedule.findIndex(x=>x.DayName == serviceScheduleAux.DayName && 
                                                        x.StartTime == serviceScheduleAux.StartTime &&
                                                        x.EndTime == serviceScheduleAux.EndTime);
    if(posArray != -1)
    {
      this.InfoServiceSchedule.splice(posArray,1);
    }
    
    event.confirm.resolve(event.newData);
  }
}
