import { Input, Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {Service} from '../../modelos/service.model';
import {ServiceType} from '../../modelos/servicetype.model';
import {Coordinate} from '../../modelos/coordinate.model';

import { GeocodeService } from './../../services/geocode.service';
import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';

@Component({
  selector: 'app-reginfoubicacion',
  templateUrl: './InformacionUbicacion.component.html',
  styleUrls: ['./InformacionUbicacion.component.css']
})

export class InformacionUbicacionComponent implements OnInit {
  @Input()searchAddress : string;
  public InfoService: Service;
  public InfoCoordinate : Coordinate;
  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  
  static IsEquasError:boolean = false;
  mensaje: any = [];
  serviceType : ServiceType[] = [];

  latA = -34.754764;
  lngA = 149.736246;
  zoom = 6;

  styles: any = [
    {
      featureType: 'all',
      stylers: [
        {
          saturation: -80
        }
      ]
    },
    {
      featureType: 'road.arterial',
      elementType: 'geometry',
      stylers: [
        {
          hue: '#00ffee'
        },
        {
          saturation: 50
        }
      ]
    },
    {
      featureType: 'poi.business',
      elementType: 'labels',
      stylers: [
        {
          visibility: 'off'
        }
      ]
    }
  ];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    private cacheManager: CacheStorageService, private storage: StorageServiceService, 
    private geocodeService: GeocodeService,
    private ref: ChangeDetectorRef,
    private genStorageServ : GenericStorageService<Service>,
    private genStorageCoord : GenericStorageService<Coordinate>) 
  { 
    this.NombreTipoServicio = this.rutaActiva.snapshot.params.tipoServicio;
    this.UrlPrev = "/publicaciones/DescripcionEspacio" + '/' + this.NombreTipoServicio;
    this.UrlNext = "/publicaciones/TransporteCercano" + '/' + this.NombreTipoServicio;
  }

  ngOnInit() {
    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    
    if(!this.InfoService)
      this.InfoService = new Service();

    this.InfoCoordinate = this.genStorageCoord.getInformation("InfoCoordinate_"+this.NombreTipoServicio);
    if(!this.InfoCoordinate || this.InfoCoordinate == null)
    {
        this.InfoCoordinate = new Coordinate();
    }
    else
    {
        this.InfoCoordinate.Latitude = this.InfoCoordinate.Latitude == "0" ? this.latA.toString() : this.InfoCoordinate.Latitude ;
        this.InfoCoordinate.Longitude = this.InfoCoordinate.Longitude == "0" ? this.lngA.toString() : this.InfoCoordinate.Longitude ;  
    }

    this.searchAddress = this.InfoService.City + ", " + this.InfoService.Address;
    this.addressToCoordinates(this.InfoService.Address);
  }

  public GuardarInfo() : boolean
  {
    if(!this.InfoCoordinate || this.InfoCoordinate == null ||
      !this.InfoCoordinate.Latitude || this.InfoCoordinate.Latitude == "0" || this.InfoCoordinate.Latitude == "" || 
      !this.InfoCoordinate.Longitude || this.InfoCoordinate.Longitude == "0" || this.InfoCoordinate.Longitude == "")
      {
          this.mensaje = [{ id:1, type:'danger', message: "Seleccione primero una ubicacin en el mapa" }];  
          return false;
      }

    this.genStorageCoord.setInformation("InfoCoordinate_"+this.NombreTipoServicio, this.InfoCoordinate);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
    return true;
  }

  public Continuar()
  {
    if(!this.GuardarInfo())
      return;

    this.router.navigate([this.UrlNext]);
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }

  private addressToCoordinates(address : string) {
    this.geocodeService.geocodeAddress(address)
    .subscribe((newLocation: Coordinate) => {
        if(newLocation != null && newLocation.Latitude != "0" && newLocation.Longitude != "0")
        {
            this.InfoCoordinate = newLocation;
            this.ref.detectChanges();  
        }
        else
        {
          this.mensaje = [{ id:1, type:'warning', message: "No se ha podido encontrar coordenadas apropiadas para el criterio de bsqueda!" }];
        }
      }      
    );     
  }

  public BuscarDireccion()
  {
    this.addressToCoordinates(this.InfoService.Address);
  }

  public getMapClick(e)
  {
    console.log(e.coords.lat, e.coords.lng);
    this.InfoCoordinate.Latitude = e.coords.lat;
    this.InfoCoordinate.Longitude = e.coords.lng;
  }
}
