import { Input, Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { Router } from '@angular/router';

import {Service} from '../../modelos/service.model';
import {AccommodationSpace} from '../../modelos/accommodationspace.model';
import {SpaceType} from '../../modelos/spacetype.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { ListaSeleccionComponent } from 'src/app/utilidades/ListaSeleccion/ListaSeleccion.component';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import { AccommodationSpaceType } from 'src/app/modelos/accommodationspacetype.model';
import { ExclusivenessType } from 'src/app/modelos/exclusivenesstype.model';

@Component({
  selector: 'app-regservalojam',
  templateUrl: './ServiciosAlojamiento.component.html',
  styleUrls: ['./ServiciosAlojamiento.component.css','./../publicaciones.component.css']
})

export class ServiciosAlojamientoComponent implements OnInit 
{
  InfoService: Service;
  InfoSpaceType: SpaceType;

  Configuration: any;
  InfoAccommodationSpace: AccommodationSpace[] = [];
  @ViewChild(ListaSeleccionComponent) compListaSpaceType: ListaSeleccionComponent<AccommodationSpace,AccommodationSpaceType	>;

  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  public NombreTipoAlojamiento : string;

  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private router:Router,
    config: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, 
    private storage: StorageServiceService, 
    private genStorageServ : GenericStorageService<Service>,
    private genStorageSpaceType : GenericStorageService<SpaceType[]>,
    private genStorageExclusivenessType : GenericStorageService<ExclusivenessType[]>,
    private genStorageAccommodationSpace : GenericStorageService<AccommodationSpace[]>) 
  { 
    this.NombreTipoServicio = "Alojamiento";
    this.UrlPrev = "/publicaciones/TipoAlojamiento";
    this.UrlNext = "/publicaciones/CapacidadAlojamiento";

    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();
      
    var auxSpaceType : SpaceType[] = this.genStorageSpaceType.getInformation("InfoSpaceType_"+this.NombreTipoServicio);
    this.InfoSpaceType = (!auxSpaceType || auxSpaceType == null || auxSpaceType.length <= 0) ? new SpaceType() : auxSpaceType[0];
    this.cacheManager.getData<SpaceType>("SpaceType", (array : SpaceType[]) => 
    { 
      var indexPos = array.findIndex(x=>x.IdSpaceType == this.InfoSpaceType.IdSpaceType);
      if(indexPos != 1)
      {
        this.NombreTipoAlojamiento = array[indexPos].Name;
      }
    });

    this.Configuration = 
    {
        NameFieldId: "IdAccommodationSpaceType",
        StorageKey: "InfoAccommodationSpace",
        MasterEntityName : "AccommodationSpaceType",
        AdditionalFieldName : "",
        AdditionalFieldType : "",
        AdditionalFieldLabel: "",
        Filters: [{field: "IdSpaceType", value: this.InfoSpaceType.IdSpaceType}],
        EsSeleccionUnica: true
    };
    this.compListaSpaceType = new ListaSeleccionComponent<AccommodationSpace,AccommodationSpaceType>(cacheManager, genStorageAccommodationSpace);
  }

  ngOnInit() 
  {
  }

  public GuardarInfo() 
  {    
    this.GetStorageEntity();
    this.genStorageAccommodationSpace.setInformation("InfoAccommodationSpace",this.InfoAccommodationSpace);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public Continuar()
  {
    if (this.compListaSpaceType.InfoStorageEntity == null || this.compListaSpaceType.InfoStorageEntity.length <= 0)
    {
        this.mensaje = [{ id:1, type:'warning', message: "Seleccione el tipo de " + this.NombreTipoAlojamiento + " de su alojamiento!" }];  
        return;
    }
    
    this.GuardarInfo();
    this.router.navigate([this.UrlNext]);
  }

  public GetStorageEntity()
  {
      var InfoExclusivenessType : ExclusivenessType[] = this.genStorageExclusivenessType.getInformation("InfoExclusivenessType_"+this.NombreTipoServicio);

      var listaEntidad : AccommodationSpace[] = [];
      this.compListaSpaceType.InfoStorageEntity.forEach(itemComp => 
      {
          var itemEntidad : AccommodationSpace = new AccommodationSpace();
          itemEntidad.IdAccommodationSpaceType = itemComp.IdAccommodationSpaceType;
          itemEntidad.PeopleCount = itemComp.PeopleCount;
          itemEntidad.RoomCount = itemComp.RoomCount;
          itemEntidad.IdExclusivenessType = InfoExclusivenessType[0].IdExclusivenessType;
          
          listaEntidad.push(itemEntidad);
      });

      this.InfoAccommodationSpace = listaEntidad;
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }

}
