import { Input, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {Router} from '@angular/router';

import {User} from '../../modelos/user.model';
import {Session} from '../../modelos/session.model';
import {Country} from '../../modelos/country.model';
import { RegisterResponse } from './../../modelos/Response/RegisterResponse.model';

import {UserService} from './../../services/user.service';
import {StorageServiceService} from '../../services/storage-service.service';
import {Md5} from 'ts-md5/dist/md5';
import { BasicInformation } from 'src/app/modelos/BasicInformation.model';
import { GenericResponse } from 'src/app/modelos/Response/GenericResponse.model';
import { SearchServiceService } from 'src/app/services/Search-service.service';


@Component({
  selector: 'app-publicaciones',
  templateUrl: './list.component.html',
  styleUrls: ['./list2.component.css']
})

export class ListComponent implements OnInit 
{
    isSpinnerVisible = true;
    ListBasicInformation_CW: BasicInformation[] = [];
    ListBasicInformation_AC: BasicInformation[] = [];
    ListBasicInformation_HT: BasicInformation[] = [];
    mensaje: any = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private router:Router,
    private serviceuser: UserService, private storage: StorageServiceService,
    private searchService: SearchServiceService)
  {
        this.isSpinnerVisible = true;
        this.ConsultarCoWorking();
        this.ConsultarAlojamientos();
        this.ConsultarHoteles();
  }

  ngOnInit() {
  }


  InitCoworking(){
    this.router.navigate(['publicaciones/NombreEspacio/CoWorking']);
  }

  InitHotel(){
    this.router.navigate(['publicaciones/NombreEspacio/Hotel']);
  }

  InitAlojamiento(){
    this.router.navigate(['publicaciones/NombreEspacio/Alojamiento']);
  }

  InitMisReservasProveedor()
  {
    this.router.navigate(['reservas/MisReservasProveedor']);
  }

  ConsultarCoWorking()
  {
        var results1$: Observable<GenericResponse<BasicInformation>>;
        results1$ = this.searchService.MisPublicaciones(true, false, false);
        results1$.subscribe(respuesta=>
        {
            if(respuesta && respuesta != null && respuesta.IsProcesed && respuesta.JSonResult && respuesta.JSonResult != null)
            {
                this.ListBasicInformation_CW = respuesta.JSonResult;
                this.isSpinnerVisible = false;
            }
            else{
                this.mensaje = [{ id:1, type:'warning', message: respuesta.Message }];
                this.isSpinnerVisible = false;
            }
        },
        error=>
        {
            this.mensaje = [{ id:1, type:'danger', message:error.tatusText }];
            this.isSpinnerVisible = false;
        });
  }

    ConsultarAlojamientos()
    {
        var results2$: Observable<GenericResponse<BasicInformation>>;
        results2$ = this.searchService.MisPublicaciones(false, false, true);
        results2$.subscribe(respuesta=>
        {
            if(respuesta && respuesta != null && respuesta.IsProcesed && respuesta.JSonResult && respuesta.JSonResult != null)
            {
                this.ListBasicInformation_AC = respuesta.JSonResult;
                this.isSpinnerVisible = false;
            }
            else{
                this.mensaje = [{ id:2, type:'warning', message: respuesta.Message }];
                this.isSpinnerVisible = false;
            }
        },
        error=>
        {
            this.mensaje = [{ id:2, type:'danger', message:error.tatusText }];
            this.isSpinnerVisible = false;
        });
    }

    ConsultarHoteles()
    {
        var results3$: Observable<GenericResponse<BasicInformation>>;
        results3$ = this.searchService.MisPublicaciones(false, true, false);
        results3$.subscribe(respuesta=>
        {
            if(respuesta && respuesta != null && respuesta.IsProcesed && respuesta.JSonResult && respuesta.JSonResult != null)
            {
                this.ListBasicInformation_HT = respuesta.JSonResult;
                this.isSpinnerVisible = false;
            }
            else{
                this.mensaje = [{ id:3, type:'warning', message: respuesta.Message }];
                this.isSpinnerVisible = false;
            }
        },
        error=>
        {
            this.mensaje = [{ id:3, type:'danger', message:error.tatusText }];
            this.isSpinnerVisible = false;
        });
    }
}

export interface IAlert {
  id: number;
  type: string;
  message: string;
}
