import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {Observable} from 'rxjs';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';

import { PublicacionesServiceService } from './../../services/Publicaciones-service.service';
import { RegisterResponse } from 'src/app/modelos/Response/RegisterResponse.model';

@Component({
  selector: 'app-resregistro',
  templateUrl: './ResumenRegistro.component.html',
  styleUrls: [ './ResumenRegistro.component.css']
})

export class ResumenRegistroComponent implements OnInit 
{
  public NombreTipoServicio : string;
  results$: Observable<RegisterResponse>;
  
  public isSpinnerVisible = false;
  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(private rutaActiva: ActivatedRoute, private router:Router, config1: NgbProgressbarConfig,
    private servicePublicaciones: PublicacionesServiceService) 
  { 
    this.NombreTipoServicio = this.rutaActiva.snapshot.params.tipoServicio;

    config1 = new NgbProgressbarConfig();
    config1.max = 80;
    config1.striped = true;
    config1.animated = true;
    config1.type = 'success';
  }

  ngOnInit() 
  {
  }

  public GuardarRegistro()
  {
    this.isSpinnerVisible = true;
    this.servicePublicaciones.Create(this.NombreTipoServicio).subscribe(respuesta=>
    {
      this.isSpinnerVisible = false;
      var registroResponse: RegisterResponse = respuesta;
      if(registroResponse != null && registroResponse.Exitoso)
      {
          this.mensaje = [{ id:2, type:'success', message: "Su registro se ha efectuado con el siguiente ID:" + registroResponse.GuidResultado }];
          this.servicePublicaciones.BorrarInformacionMemoria(); 
          this.router.navigate(["/publicaciones/list"]);         
      }
      else{
          this.mensaje = [{ id:2, type:'warning', message: registroResponse.MensajeResultado }];
      }
    },
    error=>
    {
      this.isSpinnerVisible = false;
      this.mensaje = [{
         id:2,
         type:'danger',
         message:error.statusText
      }];
    });
  }

  public Regresar()
  {
      var UrlPrev: string;
      if(this.NombreTipoServicio == "Alojamiento")
      {
        UrlPrev = "/publicaciones/DetalleAlojamiento/0/3";      
      }
      else if(this.NombreTipoServicio == "CoWorking")
      {
        UrlPrev = "/publicaciones/DetalleCoWorking/0/4";
      }
      else if(this.NombreTipoServicio == "Hotel")
      {
        UrlPrev = "/publicaciones/ZonasComunes/Hotel";
      }

      this.router.navigate([UrlPrev]);
  }
}
