import { Input, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {Country} from '../../modelos/country.model';
import {Service} from '../../modelos/service.model';
import {ServiceType} from '../../modelos/servicetype.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import { debugOutputAstAsTypeScript } from '@angular/compiler';

@Component({
  selector: 'app-reginfobasica',
  templateUrl: './InformacionBasica.component.html',
  styleUrls: ['./../publicaciones.component.css']
})

export class InformacionBasicaComponent implements OnInit {
  @Input()InfoService: Service;
  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  
  formularioInfoBasica: FormGroup;
  static IsEquasError:boolean = false;
  mensaje: any = [];
  country : Country[] = [];
  serviceType : ServiceType[] = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    config: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, private storage: StorageServiceService, 
    private genStorage : GenericStorageService<Service>) 
  { 
    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.NombreTipoServicio = this.rutaActiva.snapshot.params.tipoServicio;
    this.UrlPrev = "/publicaciones/NombreEspacio"  + '/' + this.NombreTipoServicio;
    this.UrlNext = "/publicaciones/DescripcionEspacio"  + '/' + this.NombreTipoServicio;

    this.InfoService = this.genStorage.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

    this.cacheManager.getData<ServiceType>("ServiceType", (array : ServiceType[]) => 
    { 
        this.serviceType = array; 
        this.serviceType.forEach(element => 
        {
          if(element.Name.toUpperCase() == this.NombreTipoServicio.toUpperCase())
              this.InfoService.IdServiceType = element.IdServiceType;
        });
    });

    this.getCountrys();
  }

  ngOnInit() 
  {
      this.formularioInfoBasica = this.fb.group({
        SelectCountry: ['', [Validators.required]],
        City: ['', [Validators.required]],
        Address: ['', [Validators.required]],
        PostalCode: ['', [Validators.required]],
        AditionalInfo: ['', null]
      });
  }

  get f()
  {
    let respuesta = this.formularioInfoBasica;
    return respuesta.controls;
  }

  private getCountrys() {
    this.cacheManager.getData<Country>("Country", (array : Country[]) => { this.country = array; });
  }

  public GuardarInfo() {
  	if (this.formularioInfoBasica.invalid){
      return;
    }    
    this.genStorage.setInformation("InfoService_"+this.NombreTipoServicio,this.InfoService);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public Continuar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlNext]);
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }
}
