import { Input, Component, ViewChild, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {Service} from '../../modelos/service.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';
import { HotelSpace } from 'src/app/modelos/hotelspace.model';
import { CalificacionComponent } from 'src/app/utilidades/calificacion/calificacion.component';

@Component({
  selector: 'app-regservcowork',
  templateUrl: './ClasificacionHotel.component.html',
  styleUrls: ['./ClasificacionHotel.component.css','./../publicaciones.component.css']
})

export class ClasificacionHotelComponent implements OnInit 
{
  InfoService: Service;

  datosRate:  any = {};
  InfoClasificacionHotel: number;
  @ViewChild(CalificacionComponent) compCalificacion: CalificacionComponent;

  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  
  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    config: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, private storage: StorageServiceService, 
    private genStorageServ : GenericStorageService<Service>,
    private genStorageClasificacion : GenericStorageService<number>) 
  { 
    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.compCalificacion = new CalificacionComponent();

    this.NombreTipoServicio = "Hotel";
    this.UrlPrev = "/publicaciones/HorariosAtencion" + '/' + this.NombreTipoServicio;
    //this.UrlNext = "/publicaciones/ServiciosHotel";
    this.UrlNext = "/publicaciones/ImagenPrincipal/" + this.NombreTipoServicio;

    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

    this.InfoClasificacionHotel = this.genStorageClasificacion.getInformation("ClasificacionHotel");
    if(this.InfoClasificacionHotel == null)
        this.InfoClasificacionHotel = 3;

    this.datosRate = 
    {
        nStart:   this.InfoClasificacionHotel,
        maxStar:  7
    };
  }

  ngOnInit() 
  {
  }

  public GuardarInfo() 
  {    
    if(this.compCalificacion.currentRate <= 0)
    {
        this.mensaje = [{ id:1, type:'warning', message: "Selecciona una clasificacin utilizando las estrellas!" }];  
        return;
    }

    this.getRates();
    this.genStorageClasificacion.setInformation("ClasificacionHotel",this.InfoClasificacionHotel);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  getRates()
  {
      this.InfoClasificacionHotel = this.compCalificacion.currentRate;
  }

  public Continuar()
  {    
    this.GuardarInfo();
    this.router.navigate([this.UrlNext]);
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }
}
