import { Input, Component, ViewChild, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {Service} from '../../modelos/service.model';
import {BenefitType} from '../../modelos/benefittype.model';
import {ServiceBenefit} from '../../modelos/servicebenefit.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { ListaSeleccionComponent } from 'src/app/utilidades/ListaSeleccion/ListaSeleccion.component';
import { NgbProgressbarConfig } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-regservcowork',
  templateUrl: './ZonasComunes.component.html',
  styleUrls: ['./ZonasComunes.component.css','./../publicaciones.component.css']
})

export class ZonasComunesComponent implements OnInit 
{
  InfoService: Service;

  Configuration: any;
  InfoServiceBenefit: ServiceBenefit	[] = [];
  @ViewChild(ListaSeleccionComponent) compListaTipoBeneficio: ListaSeleccionComponent<ServiceBenefit,BenefitType>;

  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;
  
  static IsEquasError:boolean = false;
  mensaje: any = [];

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private rutaActiva: ActivatedRoute, private router:Router,
    config: NgbProgressbarConfig,
    private cacheManager: CacheStorageService, private storage: StorageServiceService, 
    private genStorageServ : GenericStorageService<Service>,
    private genStorageServiceBenefit : GenericStorageService<ServiceBenefit[]>) 
  { 
    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.NombreTipoServicio = this.rutaActiva.snapshot.params.tipoServicio;
    this.UrlPrev = "/publicaciones/HorariosAtencion" + '/' + this.NombreTipoServicio;
    this.UrlNext = "/publicaciones/ResumenRegistro" + '/' + this.NombreTipoServicio;

    if(this.NombreTipoServicio == "Hotel")
    {
      this.UrlPrev = "/publicaciones/CapacidadHotel";
    }

    this.InfoService = this.genStorageServ.getInformation("InfoService_"+this.NombreTipoServicio);
    if(!this.InfoService)
      this.InfoService = new Service();

    this.Configuration = 
    {
        NameFieldId: "IdBenefitType",
        StorageKey: "InfoServiceBenefit_" + this.NombreTipoServicio,
        MasterEntityName : "BenefitType",
        AdditionalFieldName : "",
        AdditionalFieldType : "",
        AdditionalFieldLabel: "",
        FiltersOperator: "OR",
        Filters: [{field: "Category", value: this.NombreTipoServicio}, {field: "Category", value: ""} ]
    };
    this.compListaTipoBeneficio = new ListaSeleccionComponent<ServiceBenefit,BenefitType>(cacheManager, genStorageServiceBenefit);
  }

  ngOnInit() 
  {
  }

  public GuardarInfo() 
  {    
    this.GetStorageEntity();
    this.genStorageServiceBenefit.setInformation("InfoServiceBenefit_"+this.NombreTipoServicio,this.InfoServiceBenefit);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public GetStorageEntity()
  {
      var listaEntidad : ServiceBenefit[] = [];
      this.compListaTipoBeneficio.InfoStorageEntity.forEach(itemComp => 
      {
          var itemEntidad : ServiceBenefit = new ServiceBenefit();
          itemEntidad.IdBenefitType = itemComp.IdBenefitType;
          
          listaEntidad.push(itemEntidad);
      });

      this.InfoServiceBenefit = listaEntidad;
  }


  public Continuar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlNext]);
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }
}
