import { Input, Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import {Service} from '../../modelos/service.model';
import {ServiceAditionals} from '../../modelos/serviceaditionals.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from './../../services/genericstorage.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { NgbProgressbarConfig, NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-regservadic',
  templateUrl: './ServiciosAdicionales.component.html',
  styleUrls: ['./ServiciosAdicionales.component.css','./../publicaciones.component.css']
})

export class ServiciosAdicionalesComponent implements OnInit 
{
  serviceAditionals	: ServiceAditionals;
  InfoServiceAditionals	: ServiceAditionals[] = [];

  public UrlPrev : string;
  public UrlNext : string;
  public NombreTipoServicio : string;

  static IsEquasError:boolean = false;
  mensaje: any = [];

  formularioInfoAdicional: FormGroup;

  constructor(public fb: FormBuilder, public fb2: FormBuilder, private router:Router, private rutaActiva: ActivatedRoute, 
    config: NgbProgressbarConfig, private modalService: NgbModal,
    private genStorageServ : GenericStorageService<Service>,
    private genStorageServiceAditionals : GenericStorageService<ServiceAditionals[]>) 
  { 
    this.NombreTipoServicio = this.rutaActiva.snapshot.params.tipoServicio;
    this.UrlPrev = "/publicaciones/Servicios" + this.NombreTipoServicio;
    this.UrlNext = "/publicaciones/Detalle" + this.NombreTipoServicio + "/0/1";

    if(this.NombreTipoServicio == "Alojamiento")
    {
      this.UrlPrev = "/publicaciones/CapacidadAlojamiento";
      this.UrlNext = "/publicaciones/Detalle" + this.NombreTipoServicio + "/0/1";
    }

    config = new NgbProgressbarConfig();
    config.max = 100;
    config.striped = true;
    config.animated = true;
    config.type = 'success';

    this.InfoServiceAditionals = this.genStorageServiceAditionals.getInformation("InfoServiceAditionals_" + this.NombreTipoServicio);
    if(!this.InfoServiceAditionals)
      this.InfoServiceAditionals = [];
  }

  ngOnInit() 
  {
    this.formularioInfoAdicional = this.fb.group({
      Name: ['', [Validators.required]],
      Count: ['', [Validators.required, Validators.min(1)]],
      Description: ['', [Validators.required]]
    });
  }

  get f()
  {
    let respuesta = this.formularioInfoAdicional;
    return respuesta.controls;
  }

  public GuardarInfo() 
  {    
    this.genStorageServiceAditionals.setInformation("InfoServiceAditionals_" + this.NombreTipoServicio, this.InfoServiceAditionals);
    this.mensaje = [{ id:1, type:'success', message: "El estado de su publicacin ha sido guardado temporalmente. Puede retomar el registro cuando lo desee!" }];
  }

  public Continuar()
  {
      this.GuardarInfo();
      this.router.navigate([this.UrlNext]);
  }

  public Regresar()
  {
    this.GuardarInfo();
    this.router.navigate([this.UrlPrev]);
  }

  
  public MostrarFormulario(content3)
  {
    this.serviceAditionals = new ServiceAditionals();
    this.modalService.open(content3, { size: 'sm' });
  }

  public AgregarItem()
  {
    if (this.formularioInfoAdicional.invalid){
      return;
    }
    
    this.InfoServiceAditionals.push(this.serviceAditionals);
    this.modalService.dismissAll('Close click');
  }

  public EliminarItem(itemList : ServiceAditionals)
  {
      var posArray = this.InfoServiceAditionals.findIndex(x => x.Name == itemList.Name && 
                              x.Count == itemList.Count && 
                              x.Description == itemList.Description);
      if(posArray != -1)
      {
        this.InfoServiceAditionals.splice(posArray,1);
      }      
  }

}
